<template>
  <el-dialog title="" :visible.sync="dialogShow" :before-close="handleBeforeClose">
    <el-form>
      <el-form-item label=":" :label-width="formLabelWidth">
        <el-input v-model.trim.lazy="username" placeholder="" ref="username"
                  spellcheck="false"></el-input>
        <span class="prompt prompt-username" :class="showUserPrompt">*</span>
      </el-form-item>
      <el-form-item label=":" :label-width="formLabelWidth">
        <el-input v-model.trim.lazy="password" type="password" placeholder=""></el-input>
        <span class="prompt prompt-password" :class="showPasswordPrompt">*</span>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-input class="input-item" placeholder=""
                  spellcheck="false" v-model.trim.lazy="email" ref="email"></el-input>
        <span class="prompt prompt-email" :class="showEmailPrompt">*</span>
      </el-form-item>
      <!--<el-form-item label="" :label-width="formLabelWidth">-->
      <!--<el-input class="input-item" placeholder=""-->
      <!--type="text" v-model.trim.lazy="school" ref="school"></el-input>-->
      <!--<span class="prompt prompt-school" :class="showSchoolPrompt">*</span>-->
      <!--</el-form-item>-->
      <!--<el-form-item label="" :label-width="formLabelWidth">-->
      <!--<el-input class="input-item" placeholder="" type="text"-->
      <!--v-model.trim.lazy="occupation" ref="occupation"></el-input>-->
      <!--<span class="prompt prompt-occupation" :class="showOccupationPrompt">*</span>-->
      <!--</el-form-item>-->
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="clickCancel"> </el-button>
      <el-button type="primary" @click="clickConfirm"> </el-button>
    </div>
  </el-dialog>
</template>

<script type="text/ecmascript-6">
  import ElFormItem from '../../../node_modules/element-ui/packages/form/src/form-item'
  import axios from 'axios'
  import { MSG_OK, MSG_NO, baseUrl } from 'common/js/data'

  export default {
    components: {ElFormItem},
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        username: '',
        password: '',
        email: '',
//        school: '',
//        occupation: '',
        formLabelWidth: '50px',
        dialogShow: false
      }
    },
    created() {
      this.username = ''
      this.password = ''
      this.email = ''
    },
    methods: {
      clickCancel() {
        this.$emit('closeRegisterDialog')
        this._clearData()
      },
      clickConfirm() {
        let url = `${baseUrl}/users`
        console.log(this.school)
        console.log(this.occupation)
        axios.post(url, {
          'username': this.username,
          'password': this.password,
          'email': this.email
        }).then(response => {
          if (response.data.msg === MSG_OK) {
            console.log(response)
            this.$notify({
              title: '',
              message: '!',
              type: 'success'
            })
            // dialog
            this.$emit('closeRegisterDialog')
            this._clearData()
          } else if (response.data.msg === MSG_NO) {
            // 
            this.$notify({
              title: '',
              message: response.data.error,
              type: 'warning'
            })
          }
        }, response => {
          console.log(response)
          // 
          this.$notify.error({
            title: '',
            message: ',!'
          })
        })
      },
      handleBeforeClose(done) {
        this.$emit('closeRegisterDialog')
        this._clearData()
      },
      _clearData() {
        this.username = ''
        this.password = ''
        this.email = ''
//        this.school = ''
//        this.occupation = ''
      }
//      _setUserRole(userId) {
//        console.log('_setUserRole')
//        console.log(userId)
//        let url = `${baseUrl}/users/${userId}`
//        axios.put(url, {'role': 0}).then(response => {}, response => {
//          this._setUserRole(userId)
//        })
//      }
    },
    watch: {
      dialogVisible(newVal) {
        this.dialogShow = newVal
      }
    },
    computed: {
      showUserPrompt: function () {
        if (this.username.length < 6 && this.username.length !== 0) {
          return 'show-username'
        } else {
          return 'hide-username'
        }
      },
      showPasswordPrompt() {
        if (this.password.length < 6 && this.password.length !== 0) {
          return 'show-password'
        } else {
          return 'hide-password'
        }
      },
      showEmailPrompt() {
        let patt = new RegExp('([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+')
        if (!patt.test(this.email) && this.email.length !== 0) {
          return 'show-email'
        } else {
          return 'hide-email'
        }
      },
      showSchoolPrompt() {
        if (this.school.length < 2 && this.school.length !== 0) {
          return 'show-school'
        } else {
          return 'hide-school'
        }
      },
      showOccupationPrompt() {
        if (this.occupation.length < 2 && this.occupation.length !== 0) {
          return 'show-occupation'
        } else {
          return 'hide-occupation'
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-form-item
    margin-bottom 0

  .prompt
    margin-left 10px
    color #FF4949

  .prompt-username
    &.show-username
      visibility visible
    &.hide-username
      visibility hidden

  .prompt-password
    &.show-password
      visibility visible
    &.hide-password
      visibility hidden

  .prompt-email
    &.show-email
      visibility visible
    &.hide-email
      visibility hidden

</style>



